import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import "../Style/Menu.css";
import { Link, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import firebase from "../firebase.js";

function Menu() {
    const user = useSelector((state) => state.user);
    const navigate = useNavigate();

    const LogoutHandler = () => {
        firebase.auth().signOut();
        navigate("/");
    };

    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed={"top"} className={"position-sticky ps-0"}>
            <Container id="menu">
                <Navbar.Brand href="#home" className="brand">
                    
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto space">
                        <Nav.Link href="/" className="text">
                            
                        </Nav.Link>
                        <Nav.Link href="/Major" className="text">
                            
                        </Nav.Link>
                        <Nav.Link href="/Prof" className="text">
                            
                        </Nav.Link>
                        <Nav.Link href="/Academy" className="text">
                            /
                        </Nav.Link>
                        <Nav.Link href="/Lab" className="text">
                            
                        </Nav.Link>
                        <Nav.Link href="/Capston" className="text">
                            
                        </Nav.Link>
                        <NavDropdown title="" id="collasible-nav-dropdown">
                            <NavDropdown.Item href="/Guide"></NavDropdown.Item>
                            <NavDropdown.Item href="/News"></NavDropdown.Item>
                            <NavDropdown.Item href="/Board"></NavDropdown.Item>
                            <NavDropdown.Item href="/Upload"></NavDropdown.Item>
                            <NavDropdown.Item href="/List">LIST</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
                <Navbar.Collapse className="justify-content-end">
                    {user.accessToken ? (
                        <>
                            <Navbar.Text
                                style={{
                                    color: "white",
                                    cursor: "pointer",
                                    marginRight: "10px",
                                }}
                                onClick={() => LogoutHandler()}
                            >
                                Logout
                            </Navbar.Text>
                        </>
                    ) : (
                        <Link
                            to="/login"
                            style={{
                                color: "white",
                                textDecoration: "none",
                            }}
                        >
                            login
                        </Link>
                    )}
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default Menu;
