import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { LoginDiv } from "../../Style/UserCSS.js";

import firebase from "../../firebase.js";

function Login() {
    const [Email, setEmail] = useState("");
    const [PW, setPW] = useState("");
    const [ErrorMsg, setErrorMsg] = useState("");

    const user = useSelector((state) => state.user);
    let navigate = useNavigate();

    const SingInFunc = async (e) => {
        e.preventDefault();
        if (!(Email && PW)) {
            return alert("  .");
        }
        try {
            await firebase.auth().signInWithEmailAndPassword(Email, PW);
            navigate("/");
        } catch (error) {
            console.log(error.code);
            if (error.code === "auth/user-not-found") {
                setErrorMsg("  .");
            } else if (error.code === "auth/wrong-password") {
                setErrorMsg("  .");
            } else {
                setErrorMsg(" .");
            }
        }
    };

    useEffect(() => {
        if (user.accessToken) {
            navigate("/");
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        setTimeout(() => {
            setErrorMsg("");
        }, 5000);
    }, [ErrorMsg]);

    return (
        <LoginDiv>
            <form>
                <label></label>
                <input type="email" value={Email} required onChange={(e) => setEmail(e.currentTarget.value)} />
                <label></label>
                <input type="password" value={PW} required onChange={(e) => setPW(e.currentTarget.value)} />
                {ErrorMsg != "" && <p>{ErrorMsg}</p>}
                <button onClick={(e) => SingInFunc(e)}></button>
                <button
                    onClick={(e) => {
                        e.preventDefault();
                        navigate("/register");
                    }}
                >
                    
                </button>
            </form>
        </LoginDiv>
    );
}

export default Login;
